* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.country {
  border: 1px solid rgba(0, 0, 0, 0.111);
  padding: 25px;
  margin: 20px auto;
  width: 80%;
  background: #2c3e50;
  color: #ecf0f1;
  box-shadow: 0px 2px 11px rgba(44, 62, 80, 0.4);

}

.country p.error {
  color: #c0392b;
}

.country p.success {
  color: #2ecc71;
}